import {Config} from '../types';

export class ConfigStore {
  private static _config: Map<string, Config> = new Map<string, Config>();

  static getConfig(appId: string) {
    return this._config.get(appId);
  }

  static setConfig(appId: string, config: Config) {
    this._config.set(appId, {...this.getConfig(appId), ...config});
  }
}
