import {ConfigStore} from '../internal/Config';

export class Logger {
  private static instance: Logger;

  private readonly isEnabled: boolean;
  private constructor(appId: string) {
    this.isEnabled = ConfigStore.getConfig(appId).enableLogging;
  }

  public static getLogger(appId): any {
    if (!Logger.instance) {
      Logger.instance = new this(appId);
    }
    return Logger.instance;
  }

  public l(msg: string, ...args: any): void {
    if (this.isEnabled) {
      console.log(msg, ...args);
    }
  }

}
