import {mergeMap, MonoTypeOperatorFunction, timer} from 'rxjs';
import {retryBackoff} from 'backoff-rxjs';
import {ConfigStore} from './Config';

export class Utils {
  public static poll<X>(
    pollInterval: number,
    ignoreAndRetryOnError: boolean
  ): MonoTypeOperatorFunction<X> {
    return source$ =>
      timer(0, pollInterval).pipe(
        mergeMap(() => source$),
        retryBackoff({
          initialInterval: 2000,
          // maxRetries: 12,
          resetOnSuccess: true,
          shouldRetry: () => ignoreAndRetryOnError,
        })
      );
  }

  public static getBaseUrl(appId): string {
    const config = ConfigStore.getConfig(appId);
    return config.baseUrl || `https://${config.tenantId}.api.kplatform.io`;
  }
}
