import axios, {AxiosRequestHeaders} from 'axios';
import {BehaviorSubject, concat, concatMap, defer, delay, finalize, map, Observable, of, skip, tap,} from 'rxjs';
import {SSE} from 'sse.js';
import {ConfigStore} from './Config';
import {ListenerConfig} from '../types';
import {retryBackoff} from 'backoff-rxjs';
import {Utils} from './Utils';
import {Logger} from '../util/Logger';

export class HttpClient {
  private readonly _appId: string;
  private readonly logger: Logger;
  private readonly defaultListenerConfig: ListenerConfig = {
    pollingInterval: 4500,
    resumeOnError: false,
  };

  constructor(appId: string) {
    this._appId = appId;
    this.logger = Logger.getLogger(appId);
  }

  requestReply(url: string, data = {}): Observable<any> {
    return new Observable(subscriber => {
      this.initAxios(this._appId).then(client => {
        client
          .post(url, data)
          .then(res => {
            subscriber.next(res.data);
            subscriber.complete();
          })
          .catch(err => {
            subscriber.error(err);
            subscriber.complete();
          });
      });
    });
  }

  requestStream(
    url: string,
    data = {},
    config = this.defaultListenerConfig
  ): Observable<any> {
    const globalConfig = {...this.defaultListenerConfig, ...config};

    // high priority for local config
    if (config.shouldForcePolling === true) {
      return this.httpPolling(url, data, globalConfig);
    }

    // if local config set to false ignore
    if (globalConfig.shouldForcePolling && config.shouldForcePolling !== false) {
      return this.httpPolling(url, data, globalConfig);
    }
    return this.sseStream(url, globalConfig);
  }

  private httpPolling(
    url: string,
    data: any,
    config: ListenerConfig
  ): Observable<any> {
    const source$ = defer(async () => {
      const client = await this.initAxios(this._appId);
      return client.post(url, data);
    });
    const apiCall$ = source$.pipe(
      retryBackoff({
        initialInterval: 2000,
        resetOnSuccess: true,
        shouldRetry: () => config.resumeOnError,
      })
    );

    const load$ = new BehaviorSubject('');
    const whenToRefresh$ = of('').pipe(
      delay(config.pollingInterval),
      tap(() => load$.next('')),
      skip(1)
    );

    const poll$ = concat(apiCall$, whenToRefresh$);
    return load$.pipe(
      concatMap(() => poll$),
      map((res: any) => res.data)
    );
  }

  private sseStream(url: string, listenerConfig: ListenerConfig): Observable<any> {
    const baseURL = Utils.getBaseUrl(this._appId);
    const fullUrl = `${baseURL}/stream/${url}`;
    let sse;
    const terminateConnection = () => {
      sse?.close();
    }

    return new Observable(subscriber => {
      this.initSSE(fullUrl).then(eventSource => {
        sse = eventSource;
        eventSource.onmessage = event => {
          try {
            const data = JSON.parse(event.data);
            subscriber.next(data);
            this.logger.l('%c sse message - parsed >>\n', 'color: #777', data);
          } catch (e) {
            console.warn('Cannot parse SSE payload into JSON. sending as plain text. err: ', e);
            subscriber.next(event.data);
            this.logger.l('%c sse message - parsed >>\n', 'color: #777', event.data);
          }
        };
        eventSource.onerror = ev => {
          if (!listenerConfig.resumeOnError) {
            console.debug('event source error: ', ev);
            subscriber.error(ev);
          }
        };
        eventSource.stream();
      });
    }).pipe(
      finalize(terminateConnection),
    );
  }

  private async initSSE(url: string) {
    const config = ConfigStore.getConfig(this._appId);

    const headers: AxiosRequestHeaders = {};
    headers['X-TenantId'] = config.tenantId;
    if (config.getAuthTokenFn) {
      const promise = config.getAuthTokenFn();
      if (!(promise instanceof Promise)) {
        throw Error('getAuthTokenFn should be a promise.');
      }
      const token = await promise;
      if (token) {
        headers['Authorization'] = `bearer ${token}`;
      }
    }
    return new SSE(url, {headers});
  }

  //https://rxjs-78lkgd.stackblitz.io

  private async initAxios(appId: string) {
    const baseURL = Utils.getBaseUrl(appId);
    const config = ConfigStore.getConfig(appId);

    const headers: AxiosRequestHeaders = {};
    headers['Content-Type'] = 'application/json';
    headers['X-TenantId'] = config.tenantId;
    if (config.getAuthTokenFn) {
      const promise = config.getAuthTokenFn();
      if (!(promise instanceof Promise)) {
        throw Error(`${config.getAuthTokenFn.name} should be a promise.`);
      }
      const token = await promise;
      if (token) {
        headers['Authorization'] = `bearer ${token}`;
      }
    }

    const axiosInstance = axios.create({
      baseURL,
      headers,
    });
    axiosInstance.interceptors.response.use(
      config.onFulfilled,
      config.onRejected
    );
    return axiosInstance;
  }
}

export default function getHttpClient(appId: string) {
  return new HttpClient(appId);
}
