import {Config, KPlatform} from './types';
import {ServiceImpl} from './service/Service';
import {ConfigStore} from './internal/Config';

export class KPlatformImpl implements KPlatform {
  readonly id: string;
  public defaults: Config;

  private _instances: Map<string, ServiceImpl<any>>;

  constructor(defaults: Config) {
    this.id = this.generateId();
    this.defaults = defaults;
    this._instances = new Map<string, ServiceImpl<any>>();
    ConfigStore.setConfig(this.id, defaults);
  }

  getService(serviceName: string) {
    if (!this._instances.has(serviceName)) {
      this._instances.set(serviceName, new ServiceImpl(this.id, serviceName));
    }
    return this._instances.get(serviceName);
  }

  setConfig(config: Config) {
    ConfigStore.setConfig(this.id, config);
    this.defaults = config;
    console.log(this, ConfigStore.getConfig(this.id));
  }

  private generateId(): string {
    return `_${(Math.random() + 1).toString(36).substring(7)}`;
  }
}
