import {KPlatformImpl} from './KPlatformImpl';
import {Config, KPlatform} from './types';

export function initializeApp(defaultConfig: Config): KPlatform {
  if (!defaultConfig.tenantId) {
    throw Error('tenantId is required');
  }
  if (typeof defaultConfig.tenantId !== 'string') {
    throw Error('tenantId is invalid');
  }
  return new KPlatformImpl(defaultConfig);
}
