import {Observable} from 'rxjs';
import getHttpClient, {HttpClient} from '../internal/HttpClient';
import {ListenerConfig} from '../types';

export class ServiceImpl<T> implements Service<T> {
  private readonly http: HttpClient;
  private readonly _name: string;
  private _routeKey: string;
  private _version = 'v1';

  constructor(appId: string, name: string) {
    if (!name) {
      throw Error('ServiceName not defined. Please specify a ServiceName.');
    }
    this._name = name;
    this.http = getHttpClient(appId);
  }

  routeKey(routeKey: string): Service<T> {
    if (!routeKey) {
      throw Error('routeKey not defined. Please specify a routeKey.');
    }
    this._routeKey = routeKey;
    return this;
  }

  version(version: string): Service<T> {
    if (!version) {
      console.debug(
        'version not defined. Falling back to default version: ',
        this._version
      );
      return this;
    }
    this._version = version;
    return this;
  }

  execute(functionName: string, data: T): Observable<any> {
    if (!functionName) {
      throw Error('FunctionName not defined. Please specify a functionName.');
    }
    if (!this._routeKey) {
      throw Error('Route Key not defined. Please specify a routeKey.');
    }
    const url = this.buildUrl(functionName);
    return this.http.requestReply(url, data);
  }

  listen(
    functionName: string,
    data: T,
    config: ListenerConfig
  ): Observable<any> {
    if (!functionName) {
      throw Error('FunctionName not defined. Please specify a functionName.');
    }
    if (!this._routeKey) {
      throw Error('Route Key not defined. Please specify a routeKey.');
    }
    const url = this.buildUrl(functionName);
    return this.http.requestStream(url, data, config);
  }

  private buildUrl(functionName): string {
    return `${this._name}/${this._version}/${this._routeKey}/${functionName}`;
  }
}

export interface Service<T> {
  routeKey(routeKey: string): Service<T>;
  version(version: string): Service<T>;
  execute(functionName: string, data: T): Observable<any>;
  listen(
    functionName: string,
    data: T,
    config: ListenerConfig
  ): Observable<any>;
}
